import Vue from 'vue'
import VueRouter from 'vue-router'
// import Login from "../components/Login"
// import Home from "../components/Home"
// import Admin from "../components/admin/Admin"
// import User from "../components/user/User"
// import Categories from "../components/categories/Categories"
// import Order from "@/components/order/Order.vue"
// import Chain from "@/components/chain/Chain.vue"
// import ChartPage from "@/views/ChartPage"
const Login = () => import("../components/Login");
const Home = () => import("../components/Home");
const Admin = () => import("../components/admin/Admin");
const User = () => import("../components/user/User");
const Categories = () => import("../components/categories/Categories")
const Order = () => import("@/components/order/Order.vue");
const Chain = () => import("@/components/chain/Chain.vue");
const ChartPage = () => import("@/views/ChartPage")
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Login
  },
  {
    path: "/login",
    component: Login
  },
  {
    path: "/home",
    component: Home,
    redirect: "/welcome",
    children: [
      {
        path: "/admin",
        component: Admin,
      },
      {
        path: "/user",
        component: User,
      },
      {
        path: "/categories",
        component: Categories
      },
      {
        path: "/order",
        component: Order,
      },
      {
        path: "/chain",
        component: Chain,
      },
      {
        path: "/welcome",
        component: ChartPage,
      }
    ]
  },
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  if (to.path === "/login") {
    next();
  }
  else {
    if (sessionStorage.getItem("id") && sessionStorage.getItem("id").length > 0) {
      next();
    }
    else {
      next("/login");
    }
  }
})

export default router
